<template>
  <div class="mt-4">
    <loading :active.sync="isLoading"></loading>
    <div class="text-right">
      <button class="btn btn-primary" @click="openAddProductModal(true)"></button>
    </div>
    <table class="table mt-4">
      <!--  -->
      <thead>
        <tr>
          <th width="90"></th>
          <th></th>
          <th width="100" class="text-right"></th>
          <th width="100" class="text-right"></th>
          <th width="100" class="text-right"></th>
          <th width="80" class="text-right"></th>
          <th width="80" class="text-right"></th>
          <th width="80" class="text-right"></th>
        </tr>
      </thead>
      <!--  -->
      <tbody>
        <tr v-for="product in products" :key="product.id">
          <!--  -->
          <td>
            <span v-if="!product.category" class="text-danger"></span>
            <span v-else>{{ product.category }}</span>
          </td>
          <!--  -->
          <td>
            <span v-if="!product.title" class="text-danger"></span>
            <span v-else>{{ product.title }}</span>
          </td>
          <!--  -->
          <td class="text-right">
            <span
              v-if="product.stock <= 5"
              class="text-danger"
            >{{ product.stock | stock }} {{ product.unit }}</span>
            <span v-else>{{ product.stock | stock }} {{ product.unit }}</span>
          </td>
          <!--  -->
          <td class="text-right">
            <span v-if="!product.origin_price" class="text-danger"></span>
            <span v-else>{{ product.origin_price | currency }}</span>
          </td>
          <!--  -->
          <td class="text-right">
            <span v-if="!product.price" class="text-danger"></span>
            <span v-else>{{ product.price | currency }}</span>
          </td>
          <!--  -->
          <td class="text-right">
            <span v-if="product.is_enabled" class="text-success"></span>
            <span v-else class="text-danger"></span>
          </td>
          <!--  -->
          <td class="text-right">
            <button
              class="btn btn-outline-primary btn-sm"
              @click="openAddProductModal(false, product)"
            ></button>
          </td>
          <!--  -->
          <td class="text-right">
            <button
              class="btn btn-outline-danger btn-sm"
              @click="openDeleteProductModal(product)"
            ></button>
          </td>
        </tr>
      </tbody>
    </table>

    <!--  -->
    <Pagination
      class="mt-4"
      :pagination="pagination"
      @getProducts="getProducts"
      v-if="products.length !== 0"
    />
    <h3 class="mt-4 text-center" v-else></h3>

    <!--  -->
    <div
      class="modal fade"
      id="addProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    v-model="tempProduct.imageUrl"
                    type="text"
                    class="form-control"
                    id="image"
                    placeholder=""
                  />
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <i v-if="status.isFileUploading" class="fas fa-spinner fa-spin"></i>
                  </label>
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  />
                </div>
                <img
                  :src="tempProduct.imageUrl"
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid"
                  alt
                />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    v-model="tempProduct.title"
                    type="text"
                    class="form-control"
                    id="title"
                    placeholder=""
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      v-model="tempProduct.category"
                      type="text"
                      class="form-control"
                      id="category"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      v-model="tempProduct.unit"
                      type="unit"
                      class="form-control"
                      id="unit"
                      placeholder=""
                    />
                  </div>
                </div>
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      v-model="tempProduct.origin_price"
                      type="number"
                      class="form-control"
                      id="origin_price"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      v-model="tempProduct.price"
                      type="number"
                      class="form-control"
                      id="price"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      v-model="tempProduct.stock"
                      type="number"
                      class="form-control"
                      id="price"
                      placeholder=""
                    />
                  </div>
                </div>
                <hr />

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    v-model="tempProduct.description"
                    type="text"
                    class="form-control"
                    id="description"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    v-model="tempProduct.content"
                    type="text"
                    class="form-control"
                    id="content"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                      class="form-check-input"
                      type="checkbox"
                      id="is_enabled"
                    />
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button @click="updateProduct" type="button" class="btn btn-primary"></button>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div
      class="modal fade"
      id="deleteProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempProduct.title }}</strong>
            ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="deleteProduct"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

import Pagination from "./Widgets/Pagination";

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      // 
      products: [],
      //  - 
      tempProduct: {},
      // 
      pagination: {},
      // 
      isNew: false,
      // 
      isLoading: false,
      // 
      status: {
        isFileUploading: false,
      },
    };
  },
  created() {
    this.getProducts();
  },
  methods: {
    //  -------------------------------------------------------------------------
    //  
    //  -------------------------------------------------------------------------
    getProducts(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
      const vm = this;
      vm.isLoading = true;

      this.$http.get(api).then((res) => {
        if (res.data.success) {
          vm.products = res.data.products;
          vm.isLoading = false;
          vm.pagination = res.data.pagination;
        }
      });

      vm.isLoading = false;
    },

    //  -------------------------------------------------------------------------
    //   > 
    //  -------------------------------------------------------------------------
    openAddProductModal(isNew, product) {
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true;
      } else {
        this.tempProduct = Object.assign({}, product);
        this.isNew = false;
      }

      $("#addProductModal").modal("show");
    },

    //  -------------------------------------------------------------------------
    //  
    //  -------------------------------------------------------------------------
    uploadFile() {
      const uploadedFile = this.$refs.files.files[0];
      const vm = this;
      const formData = new FormData();
      vm.status.isFileUploading = true;
      formData.append("file-to-upload", uploadedFile);
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
      this.$http
        .post(url, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((res) => {
          vm.status.isFileUploading = false;

          if (res.data.success) {
            vm.$set(vm.tempProduct, "imageUrl", res.data.imageUrl);
          } else {
            vm.$bus.$emit("message:push", res.data.message, "danger");
          }
        });
    },

    //  -------------------------------------------------------------------------
    //   > 
    //  -------------------------------------------------------------------------
    updateProduct() {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
      let httpMethod = "post";
      const vm = this;

      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        httpMethod = "put";
      }

      this.$http[httpMethod](api, { data: vm.tempProduct }).then((res) => {
        if (res.data.success) {
          $("#addProductModal").modal("hide");

          vm.getProducts(vm.pagination.current_page);
        } else {
          $("#addProductModal").modal("hide");
        }
      });
    },

    //  -------------------------------------------------------------------------
    //  
    //  -------------------------------------------------------------------------
    openDeleteProductModal(product) {
      this.tempProduct = Object.assign({}, product);
      $("#deleteProductModal").modal("show");
    },

    //  -------------------------------------------------------------------------
    //  
    //  -------------------------------------------------------------------------
    deleteProduct() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${this.tempProduct.id}`;

      this.$http.delete(api).then((res) => {
        if (res.data.success) {
          console.log("");
        } else {
          console.log("");
        }

        this.getProducts();
        $("#deleteProductModal").modal("hide");
      });
    },
  },
};
</script>



