<template>
  <div class="mt-4">
    <loading :active.sync="isLoading"></loading>
    <div class="text-right">
      <button class="btn btn-primary" @click="openAddTicketModal(true)"></button>
    </div>
    <table class="table mt-4">
      <!--  -->
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th class="text-right"></th>
          <th class="text-right"></th>
          <th class="text-right"></th>
        </tr>
      </thead>
      <!--  -->
      <tbody>
        <tr v-for="ticket in tickets" :key="ticket.id">
          <!--  -->
          <td>
            <span v-if="!ticket.title" class="text-danger"></span>
            <span v-else>{{ ticket.title }}</span>
          </td>
          <!--  -->
          <td>
            <span v-if="!ticket.code" class="text-danger"></span>
            <span v-else>{{ ticket.code }}</span>
          </td>
          <!--  -->
          <td>
            <span v-if="ticket.percent === 100" class="text-danger"></span>
            <span v-else>{{ ticket.percent + '%' }}</span>
          </td>
          <!--  -->
          <td>
            <span v-if="!ticket.due_date" class="text-danger"></span>
            <span v-else>{{ ticket.due_date | date }}</span>
          </td>
          <!--  -->
          <td class="text-right">
            <span v-if="ticket.is_enabled" class="text-success"></span>
            <span v-else class="text-danger"></span>
          </td>
          <!--  -->
          <td class="text-right">
            <button
              class="btn btn-outline-primary btn-sm"
              @click="openAddTicketModal(false, ticket)"
            ></button>
          </td>
          <!--  -->
          <td class="text-right">
            <button class="btn btn-outline-danger btn-sm" @click="openDeleteTicketModal(ticket)"></button>
          </td>
        </tr>
      </tbody>
    </table>

    <!--  -->
    <Pagination
      class="mt-4"
      :pagination="pagination"
      @getTickets="getTickets"
      v-if="tickets.length !== 0"
    />
    <h3 class="mt-4 text-center" v-else></h3>

    <!--  -->
    <div
      class="modal fade"
      id="addTicketModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    v-model="tempTicket.title"
                    type="text"
                    class="form-control"
                    id="ticketTitle"
                    placeholder=""
                  />
                </div>

                <div class="form-group">
                  <label for="title"></label>
                  <input
                    v-model="tempTicket.code"
                    type="text"
                    class="form-control"
                    id="ticketCode"
                    placeholder=""
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      v-model="tempTicket.percent"
                      type="number"
                      class="form-control"
                      id="category"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      v-model="tempTicket.due_date"
                      type="date"
                      class="form-control"
                      id="unit"
                      placeholder=""
                    />
                  </div>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      v-model="tempTicket.is_enabled"
                      :true-value="1"
                      :false-value="0"
                      class="form-check-input"
                      type="checkbox"
                      id="is_enabled"
                    />
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button @click="updateTicket" type="button" class="btn btn-primary"></button>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div
      class="modal fade"
      id="deleteTicketModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempTicket.title }}</strong>
            ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="deleteTicket"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

import Pagination from "./Widgets/Pagination";

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      // 
      tickets: [],
      //  - 
      tempTicket: {},
      // 
      pagination: {},
      // 
      isNew: false,
      // 
      isLoading: false,
    };
  },
  created() {
    this.getTickets();
  },
  methods: {
    //  -------------------------------------------------------------------------
    //  
    //  -------------------------------------------------------------------------
    getTickets(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      const vm = this;
      vm.isLoading = true;

      this.$http.get(api).then((res) => {
        if (res.data.success) {
          vm.tickets = res.data.coupons;
          vm.isLoading = false;
          vm.pagination = res.data.pagination;
        }
      });
    },

    //  -------------------------------------------------------------------------
    //   > 
    //  -------------------------------------------------------------------------
    openAddTicketModal(isNew, ticket) {
      if (isNew) {
        this.tempTicket = {};
        this.isNew = true;
      } else {
        this.tempTicket = Object.assign({}, ticket);
        this.isNew = false;
      }

      $("#addTicketModal").modal("show");
    },

    //  -------------------------------------------------------------------------
    //   > 
    //  -------------------------------------------------------------------------
    updateTicket() {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      let httpMethod = "post";
      const vm = this;

      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempTicket.id}`;
        httpMethod = "put";
      }

      this.$http[httpMethod](api, { data: vm.tempTicket }).then((res) => {
        if (res.data.success) {
          $("#addTicketModal").modal("hide");

          vm.getTickets(vm.pagination.current_page);
        } else {
          $("#addTicketModal").modal("hide");
          console.log("");
        }
      });
    },

    //  -------------------------------------------------------------------------
    //  
    //  -------------------------------------------------------------------------
    openDeleteTicketModal(ticket) {
      this.tempTicket = Object.assign({}, ticket);
      $("#deleteTicketModal").modal("show");
    },

    //  -------------------------------------------------------------------------
    //  
    //  -------------------------------------------------------------------------
    deleteTicket() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${this.tempTicket.id}`;

      this.$http.delete(api).then((res) => {
        if (res.data.success) {
          console.log("");
        } else {
          console.log("");
        }

        this.getTickets();
        $("#deleteTicketModal").modal("hide");
      });
    },
  },
};
</script>


